import React, { PureComponent } from "react";
import { HOST_URL } from "../../../../../../../../../../actions/api"; 
export default class Error207 extends PureComponent {
  render() {    
    const {wall}= this.props; 
    return (
      <div className="tb_error_content">
          <div className="tb_error_title">No Posts!</div>
          <div className="tb_error_des">It seems your all posts are private. Please go to <a href={`${HOST_URL}moderation/index/${wall.Wall.id}`} target="_blank" rel="noreferrer">Moderation</a> and make some posts public.</div>
         
      </div>
    );
  }
}
