import React, { PureComponent } from "react";
import { AppUrl, CLOUD_URL } from "../../../../constants";
import { checkPreview } from "../../../../utils";

export default class PlanLimit extends PureComponent {
  render() {    
    const ErrorImage = {
      backgroundImage: `url(${CLOUD_URL}taggshop-theme/media/error/nopost.png)`
    }
    const LogoImage = {
      backgroundImage: `url(${CLOUD_URL}taggshop-theme/media/logo/taggbox-widget.svg)`
    }
    return (
      <div className="tb_error_page">
          <div className="tb_error_page_bg" style={ErrorImage}><div></div></div>
          <div className="tb_error_page_wrapper">
              <div className="tb_error_content">
                <div className="tb_error_title">You're Running Extra Widgets/Feeds!</div>
                <div className="tb_error_des">To enjoy uninterrupted services,kindly revoke the additional widget/feed or upgrade your plan.</div>
                    {/* <div className="tb_error_page_actions">
                        <div className="tb_error_action_list">
                            <a href="" className="tb_error_btn_primary">Update Now</a>
                        </div>
                    </div> */}
                </div>
                <div className="tb_error_page_logo">
                  {checkPreview() ? <span  className="tb_error_page_logo_ico"  style={LogoImage}></span> :
                    <a href={`${AppUrl}/register`} className="tb_error_page_logo_ico" style={LogoImage}></a>}
                </div>
          </div>
      </div>
    );
  }
}
