import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { getDataNextSteps } from '../../../../../../../../../../actions/themeActions/themeActions'
class SeeMore extends PureComponent {
  onLoadeMore = event => {
    const { wall, appendData } = this.props;
    const postCount = wall.Personalization.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }
  render() {
    const { wall, languageSetting, loaderData, appendData } = this.props;
    const btnDesign = { fontFamily: wall.ThemeRule.css_font,backgroundColor: wall.ThemeRule.cardColor, borderColor: wall.ThemeRule.fontColor === "#ffffff" ? 'black' : wall.ThemeRule.fontColor,color: wall.ThemeRule.fontColor === "#ffffff" ? 'black' : wall.ThemeRule.fontColor}
    
    return (
      <div className="tb_see_more_btn_wrap">
        <div className={`tb_see_more_btn tb-cTSfont-${wall.ThemeRule.font_varient} ${(loaderData && loaderData.isShowMoreLoading) ? `tb_more_btn_loading` : ``}`} >{(loaderData && loaderData.isShowMoreLoading) ? languageSetting.buttonName:languageSetting.buttonName }</div>
      </div>
    );
  }
}




const mapStateToProps = state => {
  const { appData } = state;
  return {
    loaderData: state.loaderData,
    appendData: appData.postData.appendData
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(SeeMore);