import React, { PureComponent } from "react";
import HeaderType1 from "./HeaderType/HeaderType1";
import HeaderType2 from "./HeaderType/HeaderType2";
import HeaderType3 from "./HeaderType/HeaderType3";
import HeaderType4 from "./HeaderType/HeaderType4";

export default class Header extends PureComponent {
  render() {
    const {CustomBanner} = this.props;
    return (
      <div className="tb_wall_header__ tb_wall_header_Top">
        {
          CustomBanner.bannerType == 2 ? <HeaderType1 {...this.props}/>
            : CustomBanner.bannerType == 3 ? <HeaderType2 {...this.props}/>
              : CustomBanner.bannerType == 4 ? <HeaderType3 {...this.props}/>
                 : CustomBanner.bannerType == 5 ? <HeaderType4 {...this.props}/>
                   :null}
                  
      </div>
    );
  }
}
