import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import { filterPostDataAppendWebFilter } from "../../../../../../../../../../actions/themeActions/themeActions";


class SquareFilter extends PureComponent {
  state = { networkID: null }
  onClickWebFilters = (networkID) => event => {
    const tstamp = Math.floor(Date.now() / 1000);
    const { appendData, wall, postData, wallId } = this.props;
    let postCount = wall.ThemeRule.numberOfPosts;
    this.props.filterPostDataAppendWebFilter(
      wall.Wall.id,
      tstamp,
      postCount,
      networkID,
      appendData.after,
      appendData.heightEvent,
    );
    this.setState({ networkID: networkID })
  }

  render() {
    const { webFilters, wall } = this.props;
    const { networkID } = this.state;
    return (
      <div className="tb_filter_square_wrapper">{webFilters && webFilters.length > 1 ?
        <div className="tb_filter_square_list">
          <div className={`tb_filter_square_button tb-network-ico-users ${(networkID === 0) ? `tb_filter_active` : ``}`} onClick={this.onClickWebFilters(0)}>
            <div className="tb_filter_square_ico tb__icon tb-users">
              <div></div>
            </div>
          </div>
        </div> : ''}

        {
          (webFilters && webFilters.length > 1) ? webFilters.map((item) => {
            return <div className="tb_filter_square_list" onClick={this.onClickWebFilters(item.Network.id)} key={uuidv4()}>
              <div className={`tb_filter_square_button tb-network-ico-${item.Network.name.toLowerCase()}`}>
                <div className={`tb_filter_square_ico tb__icon tb-${item.Network.id == `29` ? 'onsite' : item.Network.name.toLowerCase()} ${(networkID && networkID == item.Network.id) ? `tb_filter_active` : ``}`}>
                  <div></div>
                </div>
              </div>
            </div>
          }) : null
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { appData } = state;
  return {
    wall: appData.wall,
    webFilters: appData.webFilters,
    loaderData: state.loaderData,
    appendData: appData.postData.appendData
  }
}
const mapDispatchToProps = dispatch => {
  return {
    filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(SquareFilter);