import React, { PureComponent } from "react";
import { HOST_URL } from "../../../../../../../../../../actions/api"; 

export default class Error204 extends PureComponent {
  onClicktoUrl = (path) => event => {
    window.top.location.href = path;
}
  render() {
    const { wall } = this.props;
    return (
      <div className="tb_error_content">
        <div className="tb_error_title">Feed Not Authorized!</div>
        <div className="tb_error_des">It seems you have not authorized any Feed. Please <a
          style={{ cursor: 'pointer' }}
          onClick={this.onClicktoUrl(`${HOST_URL}wall/index/${wall.Wall.id}`)} className="manage-feed">Click here</a> to authorize Feed !</div>

      </div>
    );
  }
}
