import { v4 as uuidv4 } from 'uuid';
import React, { lazy, Suspense, PureComponent } from 'react';
import SeeMore from "./Elements/SeeMore/SeeMore";
import PoweredByLogo from "./Elements/poweredBy/brandLogo";
import { THEME_WIDTH_ADJUSTMENT_MODERN } from '../../utils'
import ThemeError from '../Components/ErrorPages/ThemeError';
import TikTok_Img_update from '../Components/customFunction/tikTok_Img_update';
const ModernSlider = lazy(() => import('./ModernSlider'));
const CollageTheme = lazy(() => import('./CollageTheme'));
const SquarePhoto = lazy(() => import('./SquarePhoto'));
const GalleryTheme = lazy(() => import('./GalleryTheme'));
const SocialCard = lazy(() => import('./SocialCard'));
const ClassicSlider = lazy(() => import('./ClassicSlider'));
const ClassicPhoto = lazy(() => import('./ClassicPhoto'));
const CircleSlider = lazy(() => import('./CircleSlider'));
const StoryTheme = lazy(() => import('./StoryTheme'));

const ThemeRender = ({ wall, loaderData, postData, adjustWidth, appData }) => {

  switch (wall.Personalization.commerceTheme) {
    case 16:
      return <Suspense fallback={null}><ModernSlider {...appData} {...postData}{...postData} adjustWidth={adjustWidth} /></Suspense>
      break;

    case 53:
      return <Suspense fallback={null}><CollageTheme {...appData} {...postData} /></Suspense>
      break;

    case 4:
      return <Suspense fallback={null}><SquarePhoto {...appData} {...postData} adjustWidth={adjustWidth} wall={wall} /></Suspense>
      break;

    case 50:
      return <Suspense fallback={null}><GalleryTheme {...appData} {...postData} /></Suspense>
      break;
    case 19:
      return <Suspense fallback={null}><SocialCard {...appData} {...postData} adjustWidth={adjustWidth} wall={wall} /></Suspense>
      break;
    case 47:
      return <Suspense fallback={null}><ClassicSlider {...appData} {...postData} loaderData={loaderData} appendData={postData.appendData} /></Suspense>
      break;
    case 3:
      return <Suspense fallback={null}><ClassicPhoto {...appData} {...postData} adjustWidth={adjustWidth} wall={wall} /></Suspense>
      break;
    case 55:
      return <Suspense fallback={null}><CircleSlider {...appData} {...postData} adjustWidth={adjustWidth} wall={wall} /></Suspense>
      break;
    case 61:
      return <Suspense fallback={null}><StoryTheme {...appData} {...postData} adjustWidth={adjustWidth} wall={wall} /></Suspense>
      break;

    default:
  }
}



 

export default class ThemeRoute extends PureComponent {
  state = {
    windowWidth: document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth:'',
    windowHeight: window.innerHeight,
    adjustWidth: 0,
    container:document.querySelector('.taggshopContainer'),
    minNumberPost:1
  }
  componentDidMount() {
    this.onResponsiveTheme();
    window.addEventListener("resize", () => {
      this.setState({ windowWidth: document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth:'', windowHeight: window.innerHeight }, () => this.onResponsiveTheme())
    });
    setTimeout(() => {this.wallNotLoadissue();}, 1300)
    this.reArrangeWall();
  }

  wallNotLoadissue=()=>{
    document.querySelector('.tb_theme_container') ? document.querySelector('.tb_theme_container') && document.querySelector('#ts_theme_container') ? console.log(".") : this.reArrangeWall() :console.log(",");
  }

  reArrangeWall=()=>{
    const resizeObserver = new ResizeObserver((entries) => {
      window.dispatchEvent(new Event('resize'));
  })
  resizeObserver.observe(document.querySelector('.website_preview'));
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.wall?.Personalization !== this.props.wall?.Personalization) {
      this.onResponsiveTheme();
    }
    if (prevProps.newPostData !== this.props.newPostData || prevProps.wall?.ThemeRule !== this.props.wall?.ThemeRule) {
      this.onResponsiveTheme();
      this.forceUpdate();
    }
  }

  checkError(themeId) {
    const { wall, appData } = this.props;
    let nmClm ;
     if(themeId==61){
       nmClm = wall.Personalization.columnCount == 3 ? 2 : wall.Personalization.columnCount == 5 ? 3 : wall.Personalization.columnCount == 7 ? 4 : wall.Personalization.columnCount == 9 ? 5 : 4;
     }
    switch (themeId) {
      case 16:
      if(appData.postData.mediaPost.length < wall.ThemeRule.multiRow){
        this.setState({minNumberPost: wall.ThemeRule.multiRow})
         return true
        }
        break;

      case 53:
        if (appData.postData.mediaPost.length < wall.ThemeRule.numberOfCoumn) {
        this.setState({minNumberPost: wall.ThemeRule.numberOfCoumn})
         return true
        }
        break;

      case 4:
        if (wall.Personalization.fitRow && (appData.postData.mediaPost.length < wall.ThemeRule.numberOfCoumn)) {
        this.setState({minNumberPost: wall.ThemeRule.numberOfCoumn})
         return true
        }
        break;

    case 50:
        if(appData.postData.mediaPost.length < 3) {
        this.setState({minNumberPost: 3})
          return true
        }
      break;

      case 61:
        if(appData.postData.mediaPost.length < nmClm) {
          this.setState({minNumberPost: nmClm})
            return true
          }
      break;

      default:
        break;
    }

  }

  onResponsiveTheme = () => {
    const { wall, loaderData, appData } = this.props;
    const { windowWidth } = this.state;
    const { adjustWidth } = THEME_WIDTH_ADJUSTMENT_MODERN(windowWidth, wall, appData.postData.postData);
    this.setState({ adjustWidth })
  }


  render() {
    const { wall, languageSetting, appData, loaderData } = this.props;
    const { adjustWidth, minNumberPost } = this.state;
    const sliderThemes = [16, 47, 55, 50, 61]
    const mediaTheme = [53, 50, 4, 16, 55, 61]
    const ShowMore_N_Filter = sliderThemes.includes(wall.Personalization.commerceTheme);
    this.checkError(wall.Personalization.commerceTheme);
    return <>
      <div className={`tb_theme_container ${wall && wall.Personalization ? "thm_"+wall.Personalization.commerceTheme :''}`}>
        { ((mediaTheme.includes(wall.Personalization.commerceTheme) && appData.postData.mediaPost.length === 0) || appData.postData.postData.length === 0) ? <ThemeError minNumberPost={minNumberPost} /> :
          adjustWidth && adjustWidth > 0 ? this.checkError(wall.Personalization.commerceTheme) ? <ThemeError minNumberPost={minNumberPost} /> : <ThemeRender {...this.props} {...this.state} appData={appData} /> : null}
        {wall && wall.UserRule && wall.UserRule.taggBranding==`1` ? <PoweredByLogo />:''}
        {appData && appData.postData && appData.postData.completeDataObject ? <TikTok_Img_update completeDataObject={appData.postData.completeDataObject} postData={appData.postData.postData} />:''}
        {wall.Personalization.loadMoreStatus && appData.postData.showMoreContent && !ShowMore_N_Filter ? <SeeMore loaderData={appData.loader} languageSetting={languageSetting} wall={wall} /> : null}  
      </div> 
    </>
  }
}
