              <path d="M48.125 40.9707V18.4824C48.125 16.7735 49.9552 15.6884 51.4546 16.5084L72.0152 27.7525C73.5758 28.6059 73.5758 30.8472 72.0152 31.7006L51.4546 42.9448C49.9552 43.7647 48.125 42.6796 48.125 40.9707Z" stroke="#14166C" strokeWidth="3" />
