import React, { PureComponent } from "react"; 
import { HOST_URL } from "../../../../../../../../../../actions/api";

export default class Error203 extends PureComponent {
  onClicktoUrl = (path) => event => {
    window.top.location.href = path;
}
  render() {
    const { wall } = this.props;
    return (
      <div className="tb_error_content">
        <div className="tb_error_title">Feed Not Activated!</div>
          <div className="tb_error_des">It seems you have not actived any Feed. Please Activate feed from your
            <a onClick={this.onClicktoUrl(`${HOST_URL}website/publish/${wall.Wall.id}`)} className="manage-feed" style={{ cursor: 'pointer' }}> wall editor</a>
         </div>
      </div>
    );
  }
}
