import React, { PureComponent } from "react";
import moment from 'moment';
export default class Error208 extends PureComponent {
  render() {
    const { wall,error } = this.props;
    return (
      <div className="tb_error_content">
        <div className="tb_error_title">No Posts!</div>
        <div className="tb_error_des">Only newly posted Instagram {error.older_days} will be displayed. Unlike other feed sources, {error.older_days}  posted in the past will not be retrieved.</div>

      </div>
    );
  }
}
