import { CUSTOM_SERVER_PATH, defaultFinalPath } from '../../../constants'

export const styleHeaderBanner=(CustomBanner, CustomBannerImage, defaultFinalPath)=>{

const mainContailer = { height: CustomBanner.banner_height, backgroundColor: CustomBanner.background_color, backgroundImage: CustomBannerImage.newName === 8661481540843.082 ? `url(${defaultFinalPath})` : `url(${CUSTOM_SERVER_PATH}${CustomBannerImage.path}${CustomBannerImage.newName})` }
const fbDiv ={ borderColor: CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#3b5998', height: 15 + CustomBanner.social_icon_size, width: 15 + CustomBanner.social_icon_size, lineHeight: `${15 + CustomBanner.social_icon_size}px` }

const twDiv = { borderColor:  CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#0F1419', height: 15 + CustomBanner.social_icon_size, width: 15 + CustomBanner.social_icon_size, lineHeight: `${15 + CustomBanner.social_icon_size}px` }

const insDiv = { borderColor:  CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#cd486b', height: 15 + CustomBanner.social_icon_size, width: 15 + CustomBanner.social_icon_size, lineHeight: `${15 + CustomBanner.social_icon_size}px` }

const fbStyle = { fontSize: CustomBanner.social_icon_size, color:  CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#3b5998' }
const twStyle = { fontSize: CustomBanner.social_icon_size, color:  CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#0F1419' }
const insStyle = { fontSize: CustomBanner.social_icon_size, color:  CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#cd486b' }

const isFb = CustomBanner.social_icons.includes("1");
const isTw = CustomBanner.social_icons.includes("2");
const isIns = CustomBanner.social_icons.includes("3");

const fbUrl = CustomBanner.facebook_url ? CustomBanner.facebook_url : `#`;
const twUrl = CustomBanner.twitter_url ? CustomBanner.twitter_url : `#`;
const insUrl = CustomBanner.instagram_url ? CustomBanner.instagram_url : `#`;
const subtitle1 = { fontFamily: CustomBanner.subtitle_css_font, fontSize: Number(CustomBanner.subTitle_font_size), color: CustomBanner.subTitle_font_color }
const subtitle2 = { fontFamily: CustomBanner.title_css_font, fontSize: Number(CustomBanner.title_font_size), color: CustomBanner.title_font_color }

return {mainContailer,fbDiv, twDiv, insDiv, fbStyle, twStyle , insStyle, isFb, isTw, isIns, fbUrl, twUrl, insUrl ,subtitle1, subtitle2}

}