import React, { PureComponent } from "react";
import { CUSTOM_SERVER_PATH } from '../../../constants'
import reactImageSize from 'react-image-size';


export default class OwnBanner extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { height: '', padding: '100', error: '', };
  }

  componentWillMount() {
    const { BannerImage } = this.props
    const imgurl =`${CUSTOM_SERVER_PATH}${BannerImage.path}${BannerImage.newName}`
    reactImageSize(imgurl)
      .then(({ width, height }) => this.setState({ height:height, width: width}))
      .catch((errorMessage) => this.setState({ height: 100, width: 100, error: errorMessage }))

  }




  render() {
    const { BannerImage } = this.props
    const { width, height } = this.state  

    const Image = { backgroundImage: `url(${CUSTOM_SERVER_PATH}${BannerImage.path}${BannerImage.newName})`, height: height+'px', width: '100%' };
    return (
      <div className="tb_custom_bannerTop"  style={Image}><div></div></div>
    );
  }
}



