import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { v4 as uuidv4 } from 'uuid'; 
import { filterPostDataAppendWebFilter } from "../../../../../../../../../../actions/themeActions/themeActions";
class CircularFilter extends PureComponent {
  state = { networkID: null }
  onClickWebFilters = (networkID) => event => {
    const tstamp = Math.floor(Date.now() / 1000);
    const { appendData, wall, postData, wallId } = this.props;
    let postCount = wall.ThemeRule.numberOfPosts;
    this.props.filterPostDataAppendWebFilter(
      wall.Wall.id,
      tstamp,
      postCount,
      networkID,
      appendData.after,
      appendData.heightEvent,
    );
    this.setState({ networkID: networkID })
  }
  
  render() {
    const { webFilters, wall, languageSetting } = this.props;
    const { networkID } = this.state;

     const font= {fontFamily: wall.ThemeRule.css_font}
    return (
      <div className="tb_filter_circular_wrapper">
        {webFilters && webFilters.length > 1 ?  
        <div className="tb_filter_circular_list" style={font}>
          <div className={`tb_filter_circular_button_text tb-cTSfont-${wall.ThemeRule.font_varient} ${(networkID === 0) ? `tb_filter_active` : ``}`} onClick={this.onClickWebFilters(0)} >{languageSetting.filterButton}</div>
        </div>:''}
        {
          (webFilters && webFilters.length > 1) ? webFilters.map((item) => {
            return <div className="tb_filter_circular_list" onClick={this.onClickWebFilters(item.Network.id)}   key={uuidv4()}>
                    <div className={`tb_filter_circular_button`} >
                      <div className={`tb_filter_round_edge_ico tb__icon tb-${item.Network.id==`29`?'onsite-upload' : item.Network.name.toLowerCase()}
                      ${(networkID && networkID === item.Network.id) ? `tb_filter_active` : ``}`}>
                        <div></div>
                      </div>
                    </div>
                  </div>
          }) : null
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { appData } = state;
  return {
    wall: appData.wall,
    webFilters: appData.webFilters,
    loaderData: state.loaderData,
    appendData: appData.postData.appendData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CircularFilter);