import React, { PureComponent } from "react";
import { ERROR_ERROR } from '../../../../constants';
export default class Error205 extends PureComponent {

  render() {
    const { wall, error } = this.props;
    return (
      <div className="tb_error_content">
            <div className="tb_error_title">No {error.post_message}!</div>
            <div className="tb_error_des">It seems there are no {error.post_message} available on the entered <div className="strongCssSpan">{ERROR_ERROR[`${error.older_post}`]}{error.older_days}</div>. All Upcoming posts will appear here.</div>
            <div className="tb_error_title">OR</div>
            <div className="tb_error_des">You can Click here to create another feed.</div>
      </div>
    );
  }
}
