import React, { PureComponent } from "react";
import { TWITTER_LOGO } from "../../../../../../../../../actions/api";
import { CUSTOM_SERVER_PATH, defaultFinalPath , LOGO_DEFAULT_PATH} from '../../../constants'
import { TWITTER_LOGO_FN } from "../../../utils";
import { styleHeaderBanner } from './HeaderConst';

export default class HeaderType3 extends PureComponent {
  render() {
    const { CustomBanner, CustomBannerImage, LogoImage } = this.props;
    const { mainContailer, fbDiv, twDiv, insDiv, fbStyle, twStyle, insStyle, isFb, isTw, isIns, fbUrl, twUrl, insUrl, subtitle1, subtitle2 } = styleHeaderBanner(CustomBanner, CustomBannerImage);
    const logo_img = LogoImage.newName ? LogoImage.newName.includes('8651481539701.51210') ? LOGO_DEFAULT_PATH : `${CUSTOM_SERVER_PATH}${LogoImage.path}${LogoImage.newName}` : LOGO_DEFAULT_PATH
    const twitterClr = CustomBanner.social_icon_color_status === 2? CustomBanner.social_icon_color: '#0F1419'
    return (
      <div className="tb_wall_header_wrap__" style={mainContailer}>
        <div className="tb_header3_row__">
          <div className="tb_header3_column_content__">
              <div className="tb_header3_content__">
                <div  className={`tb_header3_subtitle__ setFont tb-cTSfont-${CustomBanner.subtitle_font_varient}`} style={subtitle1}>{CustomBanner.subTitle} </div>
                <div className={`tb_header3_title__ setFont tb-cTSfont-${CustomBanner.title_font_varient}`} style={subtitle2}>{CustomBanner.title}</div>
              </div>
          </div>
          {CustomBanner.social_icon_status == 1 ?
            <div className="tb_header3_column_icons__">
              <div className="tb_header3_social_icons__">
                {isFb ? <div className="tb_header3_social_icon_list__" style={fbDiv} >
                <a className="tb_header1_social_icon__outer" href={fbUrl} target="_blank" ><div className="tb_header3_social_icon__ tb__icon tb-facebook"  style={fbStyle}><div></div></div></a>
                </div>:null}
                {isTw ? <div className="tb_header3_social_icon_list__" style={twDiv} >
                <a className="tb_header1_social_icon__outer" href={twUrl} target="_blank" ><div className="tb_header3_social_icon__ tb__icon tb-twitter"  style={twStyle}><TWITTER_LOGO_FN color={twitterClr}/><div></div></div></a>
                </div> :null}
                {isIns ?  <div className="tb_header3_social_icon_list__" style={insDiv} >
                <a className="tb_header1_social_icon__outer" href={insUrl} target="_blank" ><div className="tb_header3_social_icon__ tb__icon tb-instagram"  style={insStyle}><div></div></div></a>
                </div> :null}
              </div>
            </div>
            : null}
        </div>
      </div>
    );
  }
}
