import React, { PureComponent } from "react";
import { ERROR_ERROR } from '../../../../constants';
export default class Error206 extends PureComponent {

  render() {
    const { wall,error } = this.props;
    return (
      <div className="tb_error_content">
            <div className="tb_error_title">No Tweets!</div>
            <div className="tb_error_des">It seems there are no tweets available on the entered <div className="strongCssSpan">{ERROR_ERROR[`${error.older_post}`]}{error.older_days} </div>. All Upcoming tweets will appear here.</div>
      </div>
    );
  }
}
