import React from 'react'
import { Helmet } from "react-helmet";
import {TAGGSHOP_FONT_URL} from "../../../constants"



export default function index(props) {
    const { wall } = props;
    const PageStyle = wall?.Personalization?.cssStatus === 1 ? wall?.Personalization?.css : null
    //const PageTitle = `${wall.Wall.name} | Live Socialwall - Tagshop`
    const PageMetaData = null
    return <Helmet>
        {PageMetaData != null ? <meta {...PageMetaData} /> : null}
        {/* {PageTitle != null ? <title>{PageTitle}</title> : null} */}
        {PageStyle != null ? <style type="text/css">{PageStyle}</style> : null}
        {
            wall.ThemeRule && Object.keys(wall.ThemeRule).length > 0 ? <link href={`${TAGGSHOP_FONT_URL}/${wall.ThemeRule.link_font}.css`} rel="stylesheet" data-type="1" /> : null
        }
        {
            wall.UgcSettings && Object.keys(wall.UgcSettings).length > 0 ? <link href={`${TAGGSHOP_FONT_URL}/${(wall.UgcSettings.onsite_css_font_msg).replace(" ","+")}.css`} rel="stylesheet" /> : null
        }
         {
            wall.UgcSettings && Object.keys(wall.UgcSettings).length > 0 ? <link href={`${TAGGSHOP_FONT_URL}/${wall.UgcSettings.onsite_css_font_btn.replace(" ","+")}.css`} rel="stylesheet" /> : null
        }
        {
            wall.CustomBanner && Object.keys(wall.CustomBanner).length > 0 && wall.CustomBanner.subtitle_link_font && wall.CustomBanner.subtitle_link_font != "" ? <link href={`${TAGGSHOP_FONT_URL}/${wall.CustomBanner.subtitle_link_font}.css`} rel="stylesheet" /> : null
        }
        {
            wall.CustomBanner && Object.keys(wall.CustomBanner).length > 0 && wall.CustomBanner.title_link_font && wall.CustomBanner.title_link_font != "" ? <link href={`${TAGGSHOP_FONT_URL}/${wall.CustomBanner.title_link_font}.css`} rel="stylesheet" /> : null
        }
        {
            (wall.Wall && wall.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
        }
        
        {
            (wall.Wall && wall.Wall.google_tacking_status) ? <script async>
                {`window.dataLayer = window.dataLayer || [];
            function gtag() {dataLayer.push(arguments); }
            gtag("js", new Date());
            window.gaTokenClient = '${wall.Wall.google_tacking_code}';
            gtag("event", "page_view", {
                "send_to": window.gaTokenClient,
            "anonymize_ip": true,
            });`}

            </script> : null
        }

    </Helmet>
}
